      .col-12.col-md-8.col-lg-5
          br
          p.mb-3  La forma de cumplir los sueños es saber qué es lo que quiere y definir un plan para obtenerlo; así es cada persona quien tiene el poder para hacer y formar su vida.

          p.mb-3 Por ejemplo, si en el futuro se desea ser exitoso en A o B, se debe visualizar primero, para luego planificar cómo, cuándo, con quién, con qué, en qué tiempo y con qué recursos se alcanzará lo que se desea.

          p.mb-3 Puede ocurrir que los sueños se parezcan a los de otras personas, pero no todos tienen los mismos sueños, ya que nadie piensa de la misma manera. También, si el emprendedor tiene metas parecidas, puede que otras personas planifiquen de forma diferente para llegar a ellas. Es importante, entonces, fijarse metas a corto, mediano y largo plazo para no desviarse o irse por otros caminos que no conducen a ningún lugar.

    h5.mb-3 Las áreas de vida

    p.mb-5 Las áreas de vida están relacionadas con los diferentes roles que cumplen las personas en su cotidianidad. Para aumentar la calidad de vida, y lograr cumplir satisfactoriamente sus objetivos, es importante que dichas áreas se encuentren equilibradas; así, la estabilidad permite que las personas generen confianza y salud emocional para afrontar los diferentes retos de la vida.

    .row.justify-content-center.align-items-center.mb-5
      .col-12.col-lg-6
