          .h5 La visión, ¿es obligatoria?
          p El diseño de la visión no es una obligación. Se encuentra sujeta a una elección personal. Es la libertad para decidir aquello que se pretende lograr en su vida.
          p La visión, a su vez, es un compromiso. Es la manifestación del contrato que se realiza consigo mismo, con el proyecto que desea adelantar y con el entorno el cual busca impactar a través de una actividad. No es una imposición. Es una elección.

    h5.mb-3 Competencias emprendedoras

    p.mb-3 Con la formulación de la visión emprendedora, se da el primer paso en desarrollar las competencias necesarias para el comportamiento emprendedor.

    p.mb-3 El comportamiento emprendedor, dicho de manera sencilla, es la capacidad que tiene un individuo de asumir una tarea, perseverar en ella, desarrollarla y resolver cualquier contratiempo que se produjese en el camino. No es difícil ver, entonces, por qué el comportamiento emprendedor es una cualidad tan deseable en el mundo de hoy. 

    p.mb-5 <b> Una persona emprendedora pone en práctica las siguientes competencias: </b>

